<template>
  <div>
    <!-- <van-button class="roundbtn" icon="arrow-left" type="default" hairline round @click="pathgo" /> -->
    <div class="xq_h">
      <van-icon name="arrow-left" @click="tzsy" />
      <van-tabs v-model="active">
        <van-tab title="商品">
          <div class="goods">
            <van-swipe class="goods-swipe" :autoplay="3000">
              <van-swipe-item v-for="thumb in goods.thumb" :key="thumb">
                <img :src="thumb" />
              </van-swipe-item>
            </van-swipe>

            <van-cell-group>
              <van-cell>
                <div class="goods-title">{{ goods.title }}</div>
                <div class="goods-price">{{ formatPrice(goods.price) }}</div>
              </van-cell>
              <van-cell class="goods-express">
                <van-col span="10">运费：{{ goods.express }}</van-col>
                <van-col span="14">剩余：{{ goods.remain }}</van-col>
              </van-cell>
            </van-cell-group>

            <van-cell-group class="goods-cell-group">
              <van-cell value="查看作品" is-link @click="sorry">
                <template slot="title">
                  <span>作者</span>
                </template>
              </van-cell>
              <van-cell value="查看作品" is-link @click="sorry">
                <template slot="title">
                  <span>出版社</span>
                </template>
              </van-cell>
            </van-cell-group>

            <van-cell-group class="goods-cell-group">
              <van-cell title="查看商品详情" is-link @click="xqy" />
            </van-cell-group>

            <!-- <van-goods-action>
              <van-goods-action-icon icon="cart-o" @click="onClickCart">购物车</van-goods-action-icon>
              <van-goods-action-button type="warning" @click="addCar">加入购物车</van-goods-action-button>
              <van-goods-action-button type="danger" @click="sorry">立即购买</van-goods-action-button>
            </van-goods-action>-->

            <van-goods-action>
              <!-- <van-goods-action-icon icon="chat-o" text="客服" /> -->
              <van-goods-action-icon
                icon="cart-o"
                text="购物车"
                :info="this.$store.state.Cart.infonum"
                @click="onClickCart"
              />
              <!-- <van-goods-action-icon icon="shop-o" text="店铺" info="12" /> -->
              <van-goods-action-button type="warning" text="加入购物车" @click="addCar" />
              <van-goods-action-button type="danger" text="立即购买" @click="sorry" />
            </van-goods-action>
          </div>
        </van-tab>
        <van-tab title="详情">
          <div class="xq1_main">
            <img src="@/assets/libs/img/xq_img1.webp" alt />
            <img src="@/assets/libs/img/xq_img2.webp" alt />
            <img src="@/assets/libs/img/xq_img3.webp" alt />
            <img src="@/assets/libs/img/xq_img4.webp" alt />
            <img src="@/assets/libs/img/xq_img5.webp" alt />
            <img src="@/assets/libs/img/xq_img6.webp" alt />
            <img src="@/assets/libs/img/xq_img7.webp" alt />
            <img src="@/assets/libs/img/xq_img8.webp" alt />
            <img src="@/assets/libs/img/xq_img9.webp" alt />
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import {
  Tag,
  Col,
  Icon,
  Cell,
  CellGroup,
  Swipe,
  Toast,
  SwipeItem,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton
} from "vant";
export default {
  components: {
    [Tag.name]: Tag,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [GoodsAction.name]: GoodsAction,
    [GoodsActionIcon.name]: GoodsActionIcon,
    [GoodsActionButton.name]: GoodsActionButton
  },
  data() {
    return {
      // infonum:0,
      active: 0,
      goods: {},
      ajaxgoodslists: [
        {
          title:
            "限时秒杀《茶道》从喝茶到懂茶的茶类书籍 普洱茶书籍中国茶道书茶叶书籍大全 茶文化茶书茶艺茶道畅销书籍",
          price: 2680,
          express: "免运费",
          remain: 19,
          thumb: [
            "http://img3m9.ddimg.cn/63/5/1338237909-1_e_2.jpg",
            "http://img3m9.ddimg.cn/63/5/1338237909-2_e_1.jpg"
          ]
        },
        {
          title:
            "安徒生童话（统编版快乐读书吧·三年级上指定阅读，著名儿童文学家叶君健翻译）",
          price: 2680,
          express: "免运费",
          remain: 19,
          thumb: [
            "http://img3m2.ddimg.cn/39/28/23696382-1_e_1.jpg",
            "http://img3m2.ddimg.cn/39/28/23696382-3_e_1.jpg"
          ]
        },
        {
          title:
            "安徒生童话（统编版快乐读书吧·三年级上指定阅读，著名儿童文学家叶君健翻译）",
          price: 2680,
          express: "免运费",
          remain: 19,
          thumb: [
            "http://img3m4.ddimg.cn/38/0/1432382924-1_e_1.jpg",
            "http://img3m4.ddimg.cn/38/0/1432382924-3_e_1.jpg"
          ]
        },
        {
          title:
            "安徒生童话（统编版快乐读书吧·三年级上指定阅读，著名儿童文学家叶君健翻译）",
          price: 2680,
          express: "免运费",
          remain: 19,
          thumb: [
            "http://img3m9.ddimg.cn/43/34/28469869-1_e_3.jpg",
            "http://img3m9.ddimg.cn/43/34/28469869-2_e_3.jpg"
          ]
        }
      ]
    };
  },
  methods: {
    formatPrice() {
      return "¥" + (this.goods.price / 100).toFixed(2);
    },
    onClickCart() {
      this.$router.push("/gwc");
    },
    sorry() {
      Toast("加入成功");
    },
    pathgo() {
      this.$router.push("./sy");
    },
    xqy() {
      this.active = 1;
    },
    tzsy() {
      this.$router.push("/sy");
    },
    addCar() {
      this.$store.state.Cart.infonum++;
      Toast("加入成功");
      // this.infonum ++;
      this.$store.state.goods.flag = false;
      this.$store.commit("add", {
        id: this.$route.query.id,
        title: this.goods.title,
        desc: "约250g，2根",
        price: this.goods.price,
        num: 1,
        thumb: this.goods.thumb[0]
      });
    }
  },
  created() {
    window.console.log(this.$route.query.id);
    window.console.log(this.$store);
    this.goods = this.ajaxgoodslists[this.$route.query.id];
  }
};
</script>

<style lang="scss">
.van-button--hairline.roundbtn {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 999;
}
.goods {
  padding-bottom: 50px;
  &-swipe {
    img {
      width: 100%;
      display: block;
    }
  }
  &-title {
    font-size: 16px;
  }
  &-price {
    color: #f44;
  }
  &-express {
    color: #999;
    font-size: 12px;
    padding: 5px 15px;
  }
  &-cell-group {
    margin: 15px 0;
    .van-cell__value {
      color: #999;
    }
  }
  &-tag {
    margin-left: 5px;
  }
}
.xq_h {
  .xq1_main {
    padding: 0 10px;
    background-color: #fff;
    img {
      vertical-align: top;
      width: 100%;
    }
  }
  .van-icon-arrow-left {
    position: absolute;
    z-index: 999;
    height: 44px;
    line-height: 44px;
    font-size: 20px;
    left: 2%;
  }
}
</style>