<template>
  <div>
    <div class="register_main">
      <div class="register_fh">
        <span class="iconfont icon-moreinfo-copy-copy" @click="tzdl"></span>
        <span class="txt_zc">注册</span>
      </div>
      <div class="register_biaoge">
        <div class="user">
          <span class="iconfont icon-wode"></span>
          <input type="text" v-model="username" placeholder="请输入用户名" />
        </div>

        <div class="password">
          <span class="iconfont icon-mima"></span>
          <input type="text" v-model="password" placeholder="请输入密码" />
        </div>
        <div class="password1">
          <span class="iconfont icon-mima"></span>
          <input type="text" v-model="password1" placeholder="请再次输入密码" />
        </div>
        <van-button round type="info" @click="getdata">立即注册</van-button>
      </div>
    </div>
  </div>
</template>


<style lang="scss">
.register_main {
  position: fixed;
  width: 100%;
  height: 100%;
  background-size: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../assets/libs/img/login_img.webp);

  .register_fh {
    width: 100%;
    height: 40px;
    position: relative;
    .icon-moreinfo-copy-copy {
      position: absolute;
      top: 1%;
      left: 2%;
      font-size: 40px;
    }
    .txt_zc {
      font-size: 25px;
      display: block;
      width: 100%;
      height: 100%;
      line-height: 40px;
      text-align: center;
    }
  }

  .register_biaoge {
    position: absolute;
    top: 25%;
    left: 50%;
    margin-left: -122px;
    .user,
    .password,
    .password1 {
      padding: 10px;
      border: 1px solid #fff;
      border-radius: 18px;
      margin-top: 15px;
      input {
        width: 200px;
        margin-left: 5px;
        border: none;
        outline: none;
        background-color: rgba($color: #000000, $alpha: 0);
      }
    }
    .van-button--info {
      margin-top: 15px;
      border-radius: 18px;
      border: 1px solid #fff;
      width: 243.16px;
      background-color: rgba($color: #000000, $alpha: 0);
    }
  }
}
</style>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      username: null,
      password: null,
      password1: null,
      sms: "",
      iphone: ""
    };
  },
  methods: {
    tzdl() {
      this.$router.push("/login");
    },
    getdata() {
      if (this.password == this.password1) {
        this.$axios
          .get("/zc", {
            params: {
              username: this.username,
              password: this.password,
              iphone: this.iphone
            }
          })
          .then(data => {
            Toast("注册成功");
            window.console.log(data);
            this.$router.push("/login");
          });
      }
    }
  }
};
</script>