<template>
  <div class="yd">
    <!-- Swiper -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <img src="../assets/libs/img/yd1.webp" v-show="flag" class="animated rubberBand" />
          <!-- <h1 v-show="flag" class="animated jello">想要车吗</h1> -->
        </div>
        <div class="swiper-slide">
          <img src="../assets/libs/img/yd2.webp" v-show="flag" class="animated rubberBand" />
          <!-- <h1 v-show="flag" class="animated jello">想要kiting猫吗</h1> -->
        </div>
        <div class="swiper-slide">
          <img src="../assets/libs/img/yd3.webp" v-show="flag" class="animated rubberBand" />
          <h1 v-show="flag" class="animated jello">学习vue.js吧</h1>
          <button v-show="flag" class="animated jello btn_lj" @click="go">立即行动</button>
        </div>
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
import Swiper from "swiper";

export default {
  data() {
    return {
      flag: true
    };
  },
  mounted() {
    let _this = this;
    new Swiper(".swiper-container", {
      pagination: {
        el: ".swiper-pagination",
        dynamicBullets: true
      },
      on: {
        slideChangeTransitionStart: function() {
          _this.flag = false;
        },
        slideChangeTransitionEnd: function() {
          _this.flag = true;
        }
      }
    });
  },
  methods: {
    go() {
      this.$router.push("/sy");
    }
  },
  created() {
    if (localStorage.one) {
      this.$router.push("/gg");
    } else {
      localStorage.one = true;
    }
  }
};
</script>

<style>
html,
body {
  position: relative;
  height: 100%;
}
body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}
.yd .swiper-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.swiper-slide {
  display: block;
  text-align: center;
  font-size: 18px;
  background: #fff;
}
.swiper-slide img {
  width: 100%;
  height: 100%;
  /* margin-top: 150px; */
}
.btn_lj {
  position: absolute;
  bottom: 100px;
  border: 1px solid #fff;
  width: 180px;
  height: 50px;
  line-height: 50px;
  z-index: 99;
  text-align: center;
  left: 50%;
  margin-left: -90px;
  /* background-color: red; */
}
</style>