<template>
  <div>
    <div class="edit_message">
      <van-cell-group>
        <van-field v-model="username" required clearable label="用户名" placeholder="请输入用户名" />
        <van-field v-model="phone" required clearable label="手机号" placeholder="请输入手机号" />
        <van-field v-model="address" required clearable label="地址" placeholder="请输入地址" />
      </van-cell-group>
    </div>
    <van-button
      type="primary"
      size="large"
      @click="changeexit"
      v-show="!this.$store.state.edit.flag"
    >确定</van-button>
    <van-button
      type="primary"
      size="large"
      @click="change"
      v-show="this.$store.state.edit.flag"
    >确定修改</van-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      phone: "",
      address: ""
    };
  },
  created() {
    this.username = this.$store.state.edit.list[
      this.$store.state.edit.index
    ].name;
    this.phone = this.$store.state.edit.list[this.$store.state.edit.index].tel;
    this.address = this.$store.state.edit.list[
      this.$store.state.edit.index
    ].address;
    if (!this.$store.state.edit.flag) {
      this.username = "";
      this.phone = "";
      this.address = "";
    }
  },
  methods: {
    change() {
      this.$store.state.edit.list[
        this.$store.state.edit.index
      ].name = this.username;
      this.$store.state.edit.list[
        this.$store.state.edit.index
      ].tel = this.phone;
      this.$store.state.edit.list[
        this.$store.state.edit.index
      ].address = this.address;
      this.$router.push("/address");
    },
    changeexit() {
      window.console.log(this.$store.state.edit.list.length);
      this.$store.commit("setAdd", {
        id: this.$store.state.edit.list.length,
        name: this.username,
        tel: this.phone,
        address: this.address
      });

      this.$router.push("/address");
    }
  }
};
</script>