<template>
  <div>
    <div class="login_main">
      <span class="iconfont icon-moreinfo-copy-copy" @click="tzwd"></span>
      <div class="lg_title">
        <img src="@/assets/libs/img/welcome.webp" alt />
      </div>

      <div class="biaoge">
        <div class="user">
          <span class="iconfont icon-wode"></span>
          <input type="text" v-model="username" />
        </div>

        <div class="password">
          <span class="iconfont icon-mima"></span>
          <input type="text" v-model="password" />
        </div>
        <van-button round type="info" @click="getlogin">立即登陆</van-button>
        <div class="zc">
          <span @click="zhuce">注册</span>
          <span class="middle">|</span>
          <span>忘记密码</span>
        </div>
      </div>

      <!-- <van-divider>文字</van-divider> -->
    </div>
    <van-popup v-model="show" position="top" :style="{ height: '10%' }">登陆失败</van-popup>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      username: null,
      password: null,
      sms: "",
      active: 1,
      show: false
    };
  },
  methods: {
    zhuce() {
      this.$router.push("/zc");
    },
    tzwd() {
      this.$router.push("/wd");
    },
    onClickLeft() {
      this.$router.push("/wd");
    },
    getlogin() {
      // window.console.log("登录成功");
      // ajax请求后台登录API功能
      this.$axios
        .get("/login", {
          params: {
            username: this.username,
            password: this.password
          }
        })
        .then(response => {
          window.console.log(response);
          Toast("登陆成功");
          // 1.把token存储cookie或localStorage
          if (response.data.code == 200) {
            let token = response.data.result.token;
            localStorage.login = token;
            this.$router.push("/wd");
          } else {
            this.show = true;
          }
        });
    }
  }
};
</script>

<style lang="scss">
.login_main {
  position: fixed;
  width: 100%;
  height: 100%;
  background-size: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../assets/libs/img/login_img.webp);
  .icon-moreinfo-copy-copy {
    position: absolute;
    top: 1%;
    left: 2%;
    font-size: 40px;
  }
  .lg_title {
    position: absolute;
    top: 15%;
    left: 50%;
    margin-left: -40%;
    width: 80%;
    img {
      width: 100%;
    }
  }
  .biaoge {
    position: absolute;
    top: 25%;
    left: 50%;
    margin-left: -122px;
    .user,
    .password {
      padding: 10px;
      border: 1px solid #fff;
      border-radius: 18px;
      margin-top: 15px;
      input {
        width: 200px;
        margin-left: 5px;
        border: none;
        outline: none;
        background-color: rgba($color: #000000, $alpha: 0);
      }
    }
    .van-button--info {
      margin-top: 15px;
      border-radius: 18px;
      border: 1px solid #fff;
      width: 243.16px;
      background-color: rgba($color: #000000, $alpha: 0);
    }
  }
  .zc {
    width: 243.16px;
    text-align: center;
    color: #fff;
    margin-top: 15px;
    .middle {
      margin: 0 5px;
    }
  }
}
.van-popup {
  text-align: center;
}
</style>