<template>
  <div>
    <div class="h4">广告</div>
    <div class="gg_djs" @click="tosy">{{num}}秒</div>
    <div class="section">
      <img src="../assets/libs/img/gg_img1.webp" alt />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: 5
    };
  },
  created() {
    this.createi = setInterval(() => {
      if (this.num == 0) {
        this.$router.push("/sy");
      } else {
        this.num--;
      }
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.createi);
  },
  methods: {
    tosy() {
      this.$router.push("/sy");
    }
  }
};
</script>

<style lang="scss">
.gg_djs {
  position: fixed;
  top: 15px;
  right: 10px;
  z-index: 999;
  border: 1px solid black;
  padding: 5px;
  text-align: center;
  border-radius: 3px;
}
.h4 {
  position: fixed;
  top: 15px;
  left: 10px;
  z-index: 999;
}
.section {
  width: 100%;
  height: 100%;
  img {
    width: 100%;
    height: 100%;
  }
}
</style>