<template>
  <div>
    <div class="header_gwc">
      <van-nav-bar title="购物车" left-arrow @click-left="onClickLeft">
        <van-icon name="ellipsis" slot="right" />
      </van-nav-bar>
    </div>

    <!-- 没有商品时 -->
    <div class="main" v-show="isflag">
      <div class="tupian"></div>
      <p>你还没有购买任何商品</p>

      <div class="btn">
        <div class="lbtn" @click="gyg">逛一逛</div>
        <div class="rbtn">我的收藏</div>
      </div>
    </div>
    <!-- 没有商品时结束 -->

    <!-- 商品信息 -->
    <van-checkbox-group class="card-goods" v-model="checkedGoods" v-show="!isflag">
      <van-checkbox
        checked="flase"
        class="card-goods__item"
        v-for="(item,index) in goods"
        :key="index"
        :name="item.id"
        
      >
        <!--  -->
        <van-swipe-cell>
          <van-card
            :title="item.title"
            :desc="item.desc"
            :num="item.num"
            :price="formatPrice(item.price)"
            :thumb="item.thumb"
          >
            <div slot="footer">
              <van-button
                size="mini"
                @click.stop="item.num<=1?1:item.num--;$store.state.Cart.infonum<=1?1:$store.state.Cart.infonum--"
              >-</van-button>
              <span>{{item.num}}</span>
              <van-button size="mini" @click.stop="item.num++;$store.state.Cart.infonum++;">+</van-button>
            </div>
          </van-card>

          <template slot="right">
            <van-button square type="danger" text="删除" @click="del(index,item.num)" />
          </template>
        </van-swipe-cell>
        <!--  -->

        <!-- <van-card
          :title="item.title"
          :desc="item.desc"
          :num="item.num"
          :price="formatPrice(item.price)"
          :thumb="item.thumb"
        >
          <div slot="footer">
            <van-button
              size="mini"
              @click.stop="item.num<=1?1:item.num--;$store.state.Cart.infonum<=1?1:$store.state.Cart.infonum--"
            >-</van-button>
            <span>{{item.num}}</span>
            <van-button size="mini" @click.stop="item.num++;$store.state.Cart.infonum++;">+</van-button>
          </div>
        </van-card>-->

        <!-- 加减 -->
      </van-checkbox>
    </van-checkbox-group>

    <!-- 底部导航 -->
    <van-submit-bar
      :price="totalPrice"
      :disabled="!checkedGoods.length"
      :button-text="submitBarText"
      @submit="onSubmit"
    />
  </div>
</template>

<script>
import { Checkbox, CheckboxGroup, Card, SubmitBar } from "vant";
import { mapState } from "vuex";
export default {
  components: {
    [Card.name]: Card,
    [Checkbox.name]: Checkbox,
    [SubmitBar.name]: SubmitBar,
    [CheckboxGroup.name]: CheckboxGroup
  },
  data() {
    return {
      chechecked: true,
      btn_value: 1,
      value: 1,
      isflag: true,
      checkedGoods: this.$store.state.Cart.checkedGoods
    };
  },
  created() {
    this.isflag = this.$store.state.goods.flag;
    if(this.$store.state.Cart.goodslists.length==0){
      this.isflag = true;
    }
    window.console.log(this.$store.state.Cart.goodslists);
  },
  computed: {
    ...mapState({
      goods: state => state.Cart.goodslists
    }),
    submitBarText() {
      const count = this.checkedGoods.length;
      return "结算" + (count ? `(${count})` : "");
    },
    totalPrice() {
      return this.goods.reduce(
        (total, item) =>
          total +
          (this.checkedGoods.indexOf(item.id) !== -1
            ? item.price * item.num
            : 0),
        0
      );
    }
  },
  methods: {
    plus() {},
    formatPrice(price) {
      return (price / 100).toFixed(2);
    },
    onSubmit() {
      this.$store.state.Cart.checkedGoods = this.checkedGoods;
      this.$router.push("/settlement");

    },
    onClickLeft() {
      this.$router.push("/sy");
    },
    gyg() {
      this.$router.push("/sy");
    },
    del(i,num) {
      window.console.log("aa");
      this.$store.state.Cart.infonum = this.$store.state.Cart.infonum-num;
      this.checkedGoods.splice(i, 1);
      this.$store.state.Cart.goodslists.splice(i, 1);
    },
  }
};
</script>

<style lang="scss">
.header_gwc {
  .van-icon {
    color: black;
    font: 20px/1 "vant-icon";
  }
  .van-nav-bar .van-icon {
    color: black;
  }
}

.card-goods {
  padding: 10px 0;
  background-color: #fff;
  &__item {
    position: relative;
    background-color: #fafafa;
    .van-checkbox__label {
      width: 100%;
      height: auto; // temp
      padding: 0 10px 0 15px;
      box-sizing: border-box;
    }
    .van-checkbox__icon {
      top: 50%;
      left: 10px;
      z-index: 1;
      position: absolute;
      margin-top: -10px;
    }
    .van-card__price {
      color: #f44;
    }
  }
}

.main {
  .tupian {
    width: 100%;
    height: 200px;
    background-image: url("../assets/libs/img/gwc.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 200px;
  }

  p {
    width: 100%;
    text-align: center;
    font-size: 15px;
  }

  .btn {
    width: 280px;
    margin: 0 auto;

    .lbtn {
      width: 118px;
      height: 50px;
      border: 1px solid gray;
      border-radius: 5px;
      text-align: center;
      line-height: 50px;
      float: left;
      margin: 10px;
    }

    .rbtn {
      width: 118px;
      height: 50px;
      float: left;
      border: 1px solid gray;
      border-radius: 5px;
      text-align: center;
      line-height: 50px;
      margin: 10px;
    }
  }
}
</style>