import Vue from "vue";
import VueRouter from "vue-router";
import Sy from "../views/sy.vue";
import Gwc from "../views/gwc.vue";
import Fx from "../views/fx.vue";
import Gg from "../views/gg.vue";
// import Wd from "../views/wd.vue";
import Yd from "../views/yd.vue";
import Xq from "../views/xq.vue";
import Lg from "../views/login.vue";
import Ss from "../views/search.vue";
import Zc from "../views/register.vue";
import Address from "../views/address.vue";
import Edit from "../views/edit.vue";
Vue.use(VueRouter);

import settlement from "./shoppingcar/settlement.js"
import submit from "./shoppingcar/submit.js"
import aa from "./my/my.js";
import integral from "./my/integral.js";
import center from "./my/center.js";
import coupon from "./my/coupon.js";
import jf from "./my/jf.js";
import giftcard from "./my/giftcard.js";
const routes = [
  {
    path: "/",
    component: Yd
  },
  {
    path: "/address",
    component: Address
  },
  {
    path: "/edit",
    component: Edit
  },
  {
    path: "/sy",
    component: Sy
  },
  {
    path: "/zc",
    component: Zc
  },
  {
    path: "/sy/ss",
    component: Ss
  },
  {
    path: "/gwc",
    component: Gwc
  },
  {
    path: "/fx",
    component: Fx
  },
  // {
  //   path: "/wd",
  //   component: Wd
  // },
  {
    path: "/yd",
    component: Yd
  },
  {
    path: "/gg",
    component: Gg
  },
  {
    path: "/xq",
    component: Xq
  },
  {
    path: "/login",
    component: Lg
  },
  ...aa,
  ...settlement,
  ...submit,
  ...integral,
  ...center,
  ...coupon,
  ...jf,
  ...giftcard
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
