<template>
  <div>
    <div class="header_address">
      <van-nav-bar title="地址" left-arrow @click-left="onClickLeft">
        <van-icon name="ellipsis" slot="right" />
      </van-nav-bar>
    </div>

    <van-address-list
      v-show="this.$store.state.edit.isflag"
      v-model="chosenAddressId"
      :list="this.$store.state.edit.list"
      :disabled-list="this.$store.state.edit.disabledList"
      disabled-text="以下地址超出配送范围"
      add-button-text="确定"
      @add="determine"
      @edit="onEdit"
      @select="stateid"
    />

    <van-address-list
      v-show="!this.$store.state.edit.isflag"
      v-model="chosenAddressId"
      :list="this.$store.state.edit.list"
      :disabled-list="this.$store.state.edit.disabledList"
      disabled-text="以下地址超出配送范围"
      @add="onAdd"
      @edit="onEdit"
      @select="stateid"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      Id: "",
      chosenAddressId: "0"
    };
  },

  methods: {
    onAdd() {
      this.$router.push("/edit");
      this.$store.state.edit.flag = false;
    },

    onEdit() {
      this.Id = this.chosenAddressId;
      this.$store.state.edit.flag = true;
      this.$store.commit("setId", this.Id);
      this.$router.push("/edit");
    },
    onClickLeft() {
      this.$router.push("/wd");
    },
    stateid() {},
    determine() {
      this.$router.push("/settlement");
      this.$store.state.edit.index = this.chosenAddressId;
      window.console.log(this.$store.state.edit.index);
    }
  }
};
</script>

<style lang="scss">
.header_address {
  .van-icon {
    color: black;
    font: 20px/1 "vant-icon";
  }
  .van-nav-bar .van-icon {
    color: black;
  }
}
</style>